import React, { useState } from 'react';

// Field options for projects (same as in Job creation and Ticket)
const fieldOptions = [
  'Software Development', 'Data Science', 'Cybersecurity', 'DevOps', 'UI/UX Design',
  'Product Management', 'Marketing', 'Sales', 'Finance', 'Human Resources',
  'Operations', 'Customer Success', 'Business Development', 'Content Creation',
  'Graphic Design', 'Digital Marketing', 'Project Management', 'Quality Assurance',
  'System Administration', 'Network Engineering', 'Mobile Development', 'Web Development',
  'Game Development', 'Artificial Intelligence', 'Machine Learning', 'Cloud Computing',
  'Database Administration', 'Technical Writing', 'Research & Development', 'Consulting'
];

// Technical skills options (same as in Job creation)
const technicalSkillsOptions = [
  // Programming Languages
  'Node.js (JavaScript / TypeScript)',
  'Python',
  'Java',
  'C# (.NET Core)',
  'Go (Golang)',
  'PHP',
  'Ruby',
  'Rust',
  
  // Frameworks and Libraries
  'Express.js / Fastify / NestJS (Node.js)',
  'Django / Flask / FastAPI (Python)',
  'Spring Boot (Java)',
  'ASP.NET Core (C#)',
  'Laravel (PHP)',
  'Ruby on Rails (Ruby)',
  'Fiber / Gin (Go)',
  
  // Databases
  'PostgreSQL',
  'MySQL / MariaDB',
  'MongoDB',
  'Redis',
  'SQLite',
  'Firebase / Firestore',
  'Elasticsearch',
  'Prisma / TypeORM / Sequelize / Mongoose (ORM/ODM)',
  
  // Data, AI & Machine Learning
  'Python (NumPy, Pandas, Scikit-learn, TensorFlow, PyTorch)',
  'R',
  'SQL (Advanced)',
  'Jupyter / Google Colab',
  'Hugging Face / LangChain / OpenAI API',
  
  // DevOps & Cloud
  'Docker / Docker Compose',
  'Kubernetes',
  'AWS / Azure / Google Cloud Platform',
  'CI/CD (GitHub Actions, GitLab CI, Jenkins)',
  'Terraform / Ansible',
  'Nginx / Apache',
  'Linux / Bash scripting',
  
  // Tools and Work Systems
  'Git / GitHub / GitLab / Bitbucket',
  'Jira / Trello / Notion',
  'Postman / Insomnia (API Testing)',
  'Figma (UI/UX Collaboration)',
  'VS Code / IntelliJ / PyCharm',
  
  // Mobile Development
  'React Native',
  'Flutter (Dart)',
  'Swift (iOS)',
  'Kotlin (Android)',
  'Ionic / Capacitor',
  
  // Fullstack / Cross-Discipline
  'GraphQL / Apollo',
  'RESTful APIs',
  'WebSockets / Socket.io',
  'Authentication (JWT, OAuth2)',
  'Microservices Architecture'
];

const ProjectsSection = ({ projects, onAddProject, onEditProject, onDeleteProject }) => {
  const [isAdding, setIsAdding] = useState(false);
  const [isEditing, setIsEditing] = useState(false);
  const [editingProject, setEditingProject] = useState(null);
  const [selectedField, setSelectedField] = useState('');
  const [selectedTechnicalSkill, setSelectedTechnicalSkill] = useState('');
  const [formData, setFormData] = useState({
    projectName: '',
    githubLink: '',
    field: '',
    technicalSkills: []
  });

  const toggleProjectsEdit = () => {
    setIsAdding(!isAdding);
    if (!isAdding) {
      setFormData({
        projectName: '',
        githubLink: '',
        field: '',
        technicalSkills: []
      });
      setSelectedField('');
      setSelectedTechnicalSkill('');
    }
  };

  const handleInputChange = (e) => {
    const { name, value } = e.target;
    setFormData(prev => ({
      ...prev,
      [name]: value
    }));
  };

  const handleFieldChange = (e) => {
    const field = e.target.value;
    setSelectedField(field);
    setFormData(prev => ({
      ...prev,
      field: field
    }));
  };

  const handleAddTechnicalSkill = () => {
    if (selectedTechnicalSkill && !formData.technicalSkills.includes(selectedTechnicalSkill)) {
      setFormData(prev => ({
        ...prev,
        technicalSkills: [...prev.technicalSkills, selectedTechnicalSkill]
      }));
      setSelectedTechnicalSkill('');
    }
  };

  const handleRemoveTechnicalSkill = (index) => {
    setFormData(prev => ({
      ...prev,
      technicalSkills: prev.technicalSkills.filter((_, i) => i !== index)
    }));
  };

  const handleAddProject = () => {
    if (formData.projectName.trim() && formData.githubLink.trim()) {
      onAddProject(formData);
      setFormData({
        projectName: '',
        githubLink: '',
        field: '',
        technicalSkills: []
      });
      setSelectedField('');
      setSelectedTechnicalSkill('');
      setIsAdding(false);
    }
  };

  const handleCancel = () => {
    setFormData({
      projectName: '',
      githubLink: '',
      field: '',
      technicalSkills: []
    });
    setSelectedField('');
    setSelectedTechnicalSkill('');
    setIsAdding(false);
    setIsEditing(false);
    setEditingProject(null);
  };

  const handleEditProject = (project) => {
    console.log('Edit project clicked:', project);
    setEditingProject(project);
    setFormData({
      projectName: project.projectName,
      githubLink: project.githubLink,
      field: project.field || '',
      technicalSkills: project.technicalSkills || []
    });
    setSelectedField(project.field || '');
    setIsEditing(true);
    setIsAdding(false);
  };

  const handleDeleteProject = (projectName) => {
    if (window.confirm(`Are you sure you want to delete the project "${projectName}"?`)) {
      onDeleteProject(projectName);
    }
  };

  const handleUpdateProject = () => {
    if (formData.projectName.trim() && formData.githubLink.trim()) {
      onEditProject(editingProject.projectName, formData);
      setFormData({
        projectName: '',
        githubLink: '',
        field: '',
        technicalSkills: []
      });
      setSelectedField('');
      setSelectedTechnicalSkill('');
      setIsEditing(false);
      setEditingProject(null);
    }
  };

  return (
    <div className="section-card">
      <div className="section-header">
        <h3 className="section-title">Projects</h3>
        <button className="edit-btn" onClick={toggleProjectsEdit}>
          <i className="fas fa-plus"></i> Add Project
        </button>
      </div>
      {!isAdding && !isEditing ? (
        <div id="projectsDisplay">
          {projects.length === 0 ? (
            <div className="empty-state">
              <i className="fas fa-folder-open"></i>
              <p>No projects added yet. Click "Add Project" to get started!</p>
            </div>
          ) : (
            projects.map((project, index) => (
              <div key={index} className="project-item">
                <div className="project-header">
                  <div className="project-title">{project.projectName}</div>
                  <div className="project-actions">
                    <button 
                      className="btn btn-sm btn-secondary" 
                      onClick={(e) => {
                        console.log('Edit button clicked for project:', project.projectName);
                        e.preventDefault();
                        e.stopPropagation();
                        handleEditProject(project);
                      }}
                      title="Edit Project"
                      style={{ 
                        backgroundColor: '#6c757d', 
                        color: 'white', 
                        border: 'none',
                        padding: '6px 8px',
                        borderRadius: '6px',
                        cursor: 'pointer',
                        fontSize: '12px',
                        display: 'inline-flex',
                        alignItems: 'center',
                        gap: '4px',
                        zIndex: 1000,
                        position: 'relative'
                      }}
                    >
                      <i className="fas fa-edit"></i>
                    </button>
                    <button 
                      className="btn btn-sm btn-danger" 
                      onClick={() => handleDeleteProject(project.projectName)}
                      title="Delete Project"
                    >
                      <i className="fas fa-trash"></i>
                    </button>
                  </div>
                </div>
                {project.field && (
                  <div className="project-field">
                    <i className="fas fa-tag"></i> {project.field}
                  </div>
                )}
                {project.technicalSkills && project.technicalSkills.length > 0 && (
                  <div className="project-languages">
                    <i className="fas fa-code"></i> {project.technicalSkills.join(', ')}
                  </div>
                )}
                <div className="project-links">
                  {project.githubLink && (
                    <a href={project.githubLink} className="project-link" target="_blank" rel="noopener noreferrer">
                      <i className="fab fa-github"></i> View on GitHub
                    </a>
                  )}
                </div>
              </div>
            ))
          )}
        </div>
      ) : (
        <div id="projectsEdit">
          {isEditing && <h4>Edit Project: {editingProject?.projectName}</h4>}
          <div className="form-group">
            <label className="form-label">Project Name:</label>
            <input 
              type="text" 
              className="form-input" 
              id="projectNameInput" 
              name="projectName"
              value={formData.projectName}
              onChange={handleInputChange}
              placeholder="Enter project name"
              required
            />
          </div>
          <div className="form-group">
            <label className="form-label">GitHub Repository Link:</label>
            <input 
              type="url" 
              className="form-input" 
              id="projectGithubInput" 
              name="githubLink"
              value={formData.githubLink}
              onChange={handleInputChange}
              placeholder="https://github.com/username/project"
              required
            />
          </div>
          <div className="form-group">
            <label className="form-label">Project Field:</label>
            <select
              className="form-input"
              value={selectedField}
              onChange={handleFieldChange}
            >
              <option value="">Select a field</option>
              {fieldOptions.map((field, index) => (
                <option key={index} value={field}>
                  {field}
                </option>
              ))}
            </select>
          </div>
          <div className="form-group">
            <label className="form-label">Technical Skills Used:</label>
            <div className="technical-skills-input">
              <select
                className="form-input"
                value={selectedTechnicalSkill}
                onChange={(e) => setSelectedTechnicalSkill(e.target.value)}
              >
                <option value="">Select a technical skill</option>
                {technicalSkillsOptions.map((skill, index) => (
                  <option key={index} value={skill}>
                    {skill}
                  </option>
                ))}
              </select>
              <button
                type="button"
                className="btn btn-secondary btn-sm"
                onClick={handleAddTechnicalSkill}
                disabled={!selectedTechnicalSkill}
              >
                <i className="fas fa-plus"></i> Add
              </button>
            </div>
            
            {formData.technicalSkills && formData.technicalSkills.length > 0 && (
              <div className="technical-skills-list">
                <h4>Selected Technical Skills:</h4>
                <ul className="technical-skills-tags">
                  {formData.technicalSkills.map((skill, index) => (
                    <li key={index} className="technical-skill-tag">
                      <span>{skill}</span>
                      <button
                        type="button"
                        className="remove-technical-skill"
                        onClick={() => handleRemoveTechnicalSkill(index)}
                      >
                        <i className="fas fa-times"></i>
                      </button>
                    </li>
                  ))}
                </ul>
              </div>
            )}
          </div>
          <div className="form-actions">
            {isEditing ? (
              <>
                <button className="btn btn-primary" onClick={handleUpdateProject}>Update Project</button>
                <button className="btn btn-secondary" onClick={handleCancel}>Cancel</button>
              </>
            ) : (
              <>
                <button className="btn btn-primary" onClick={handleAddProject}>Add Project</button>
                <button className="btn btn-secondary" onClick={handleCancel}>Cancel</button>
              </>
            )}
          </div>
        </div>
      )}
    </div>
  );
};

export default ProjectsSection;




