import React, { useState, useEffect } from 'react';

const ProfileCard = ({ 
  profileName, 
  profileTitle, 
  aiScore, 
  availabilityStatus, 
  projectsCount, 
  applicationsCount,
  onAvatarUpload,
  onCoverUpload,
  onToggleAvailability
}) => {
  const [isAvailable, setIsAvailable] = useState(availabilityStatus === 'Available for opportunities');

  // Load availability status from localStorage on component mount
  useEffect(() => {
    const savedAvailability = localStorage.getItem('availabilityStatus');
    if (savedAvailability !== null) {
      const isAvailableFromStorage = savedAvailability === 'true';
      setIsAvailable(isAvailableFromStorage);
      onToggleAvailability(isAvailableFromStorage ? 'Available for opportunities' : 'Not available');
    }
  }, [onToggleAvailability]);

  const handleAvailabilityToggle = () => {
    const newStatus = !isAvailable;
    setIsAvailable(newStatus);
    // Save to localStorage
    localStorage.setItem('availabilityStatus', newStatus.toString());
    onToggleAvailability(newStatus ? 'Available for opportunities' : 'Not available');
  };

  const handleAvatarClick = (e) => {
    e.stopPropagation();
    document.getElementById('avatarFileInput').click();
  };

  const handleCoverClick = () => {
    document.getElementById('coverFileInput').click();
  };

  const handleAvatarChange = (e) => {
    if (e.target.files && e.target.files[0]) {
      onAvatarUpload(e.target.files[0]);
    }
  };

  const handleCoverChange = (e) => {
    if (e.target.files && e.target.files[0]) {
      onCoverUpload(e.target.files[0]);
    }
  };

  return (
    <div className="profile-card" id="profile-card" role="main" aria-label="User profile">
      <div 
        className="profile-cover" 
        onClick={handleCoverClick} 
        role="button" 
        aria-label="Click to upload cover photo" 
        tabIndex="0"
      >
        <div 
          className="profile-avatar" 
          id="profileAvatar" 
          onClick={handleAvatarClick} 
          role="button" 
          aria-label="Click to upload profile photo" 
          tabIndex="0"
        >
          <i className="fas fa-user" id="avatarIcon" aria-hidden="true"></i>
          <div className="avatar-upload-overlay" aria-hidden="true">
            <div>Click to upload photo</div>
          </div>
        </div>
        <input 
          type="file" 
          id="avatarFileInput" 
          className="file-input" 
          accept="image/*" 
          onChange={handleAvatarChange} 
          aria-label="Upload profile photo"
        />
        <input 
          type="file" 
          id="coverFileInput" 
          className="file-input" 
          accept="image/*" 
          onChange={handleCoverChange} 
          aria-label="Upload cover photo"
        />
      </div>
      <div className="profile-info">
        <h2 className="profile-name" id="profileName">{profileName || 'Your Name'}</h2>
        <p className="profile-title" id="profileTitle">{profileTitle || 'Your Professional Title'}</p>
        
        <div className="ai-score-simple">
          <div className="ai-score-number" id="aiScore">{aiScore}</div>
        </div>
        
        <div 
          className={`status-indicator ${isAvailable ? 'status-online' : 'status-unavailable'}`} 
          id="availabilityStatus" 
          onClick={handleAvailabilityToggle} 
          role="button" 
          tabIndex="0" 
          aria-label="Toggle availability status"
        >
          <i className="fas fa-circle" id="statusDot" style={{ fontSize: '8px' }}></i>
          <span id="statusText">{isAvailable ? 'Available for opportunities' : 'Not available'}</span>
        </div>
        <div className="profile-stats" role="region" aria-label="Profile statistics">
          <div className="stat" role="group" aria-label="Projects count">
            <div className="stat-number" id="projectsCount" aria-live="polite">{projectsCount}</div>
            <div className="stat-label">Projects</div>
          </div>
          <div className="stat" role="group" aria-label="Applications count">
            <div className="stat-number" id="applicationsCount" aria-live="polite">{applicationsCount}</div>
            <div className="stat-label">Applications</div>
          </div>
        </div>
      </div>
    </div>
  );
};

export default ProfileCard;
