import React, { useState } from 'react';

const CandidateTicketSection = ({ 
  ticketData, 
  onSaveTicket
}) => {
  const [isEditing, setIsEditing] = useState(false);
  const [selectedField, setSelectedField] = useState('');
  const [formData, setFormData] = useState({
    location: ticketData.location,
    experience: ticketData.experience,
    remoteWork: ticketData.remoteWork,
    jobType: ticketData.jobType,
    field: ticketData.field ? (typeof ticketData.field === 'string' ? ticketData.field.split(',').map(f => f.trim()).filter(f => f) : ticketData.field) : [],
    degree: ticketData.degree
  });

  const toggleTicketEdit = () => {
    setIsEditing(!isEditing);
    if (!isEditing) {
      setFormData({
        location: ticketData.location,
        experience: ticketData.experience,
        remoteWork: ticketData.remoteWork,
        jobType: ticketData.jobType,
        field: ticketData.field ? (typeof ticketData.field === 'string' ? ticketData.field.split(',').map(f => f.trim()).filter(f => f) : ticketData.field) : [],
        degree: ticketData.degree
      });
    }
  };

  const handleInputChange = (e) => {
    const { name, value } = e.target;
    setFormData(prev => ({
      ...prev,
      [name]: value
    }));
  };

  const handleAddField = () => {
    if (selectedField && !formData.field.includes(selectedField)) {
      setFormData(prev => ({
        ...prev,
        field: [...prev.field, selectedField]
      }));
      setSelectedField('');
    }
  };

  const handleRemoveField = (index) => {
    setFormData(prev => ({
      ...prev,
      field: prev.field.filter((_, i) => i !== index)
    }));
  };

  const handleSave = async () => {
    console.log('CandidateTicketSection handleSave called with formData:', formData);
    // Convert field array to comma-separated string for backend
    const dataToSave = {
      ...formData,
      field: formData.field.join(', ')
    };
    await onSaveTicket(dataToSave);
    setIsEditing(false);
  };

  const handleCancel = () => {
    setFormData({
      location: ticketData.location,
      experience: ticketData.experience,
      remoteWork: ticketData.remoteWork,
      jobType: ticketData.jobType,
      field: ticketData.field ? (typeof ticketData.field === 'string' ? ticketData.field.split(',').map(f => f.trim()).filter(f => f) : ticketData.field) : [],
      degree: ticketData.degree
    });
    setIsEditing(false);
  };

  const districts = [
    'Northern District',
    'Haifa District', 
    'Central District',
    'Tel Aviv District',
    'Jerusalem District',
    'Southern District'
  ];


  const remoteOptions = ['Remote', 'On-Site', 'Hybrid'];

  const jobTypes = [
    'Full-time', 'Part-time', 'Contract', 'Internship', 'Freelance'
  ];

  const fields = [
    'Software Development', 'Data Science', 'Cybersecurity', 'DevOps', 'UI/UX Design',
    'Product Management', 'Marketing', 'Sales', 'Finance', 'Human Resources',
    'Operations', 'Customer Success', 'Business Development', 'Content Creation',
    'Graphic Design', 'Digital Marketing', 'Project Management', 'Quality Assurance',
    'System Administration', 'Network Engineering', 'Mobile Development', 'Web Development',
    'Game Development', 'Artificial Intelligence', 'Machine Learning', 'Cloud Computing',
    'Database Administration', 'Technical Writing', 'Research & Development', 'Consulting'
  ];

  const degreeOptions = [
    'No Degree',
    'Bachelor\'s Degree',
    'Master\'s Degree',
    'PhD or Doctorate',
    'College Diploma',
    'Professional Certification',
    'Bootcamp Graduate',
    'High School Diploma'
  ];

  return (
    <div className="section-card candidate-ticket-section">
      <div className="section-header">
        <h3 className="section-title">Candidate Ticket</h3>
        <button 
          className="edit-btn" 
          onClick={toggleTicketEdit} 
          aria-label="Edit ticket information" 
          aria-expanded={isEditing} 
          aria-controls="ticketEditArea"
        >
          <i className="fas fa-edit" aria-hidden="true"></i> Edit
        </button>
      </div>
      {!isEditing ? (
        <div id="ticketDisplay">
          <div className="info-item">
            <div className="info-label">Location:</div>
            <div className="info-value" id="ticketLocationValue">{ticketData.location || 'Not specified'}</div>
          </div>
          <div className="info-item">
            <div className="info-label">Experience:</div>
            <div className="info-value" id="ticketExperienceValue">{ticketData.experience || 'Not specified'}</div>
          </div>
          <div className="info-item">
            <div className="info-label">Remote Work:</div>
            <div className="info-value" id="ticketRemoteValue">{ticketData.remoteWork || 'Not specified'}</div>
          </div>
          <div className="info-item">
            <div className="info-label">Job Type:</div>
            <div className="info-value" id="ticketJobTypeValue">{ticketData.jobType || 'Not specified'}</div>
          </div>
          <div className="info-item">
            <div className="info-label">Field:</div>
            <div className="info-value" id="ticketFieldValue">
              {ticketData.field ? (
                typeof ticketData.field === 'string' ? (
                  ticketData.field.split(',').map(f => f.trim()).filter(f => f).length > 0 ? (
                    <ul className="field-list">
                      {ticketData.field.split(',').map(f => f.trim()).filter(f => f).map((field, index) => (
                        <li key={index}>{field}</li>
                      ))}
                    </ul>
                  ) : 'Not specified'
                ) : (
                  ticketData.field.length > 0 ? (
                    <ul className="field-list">
                      {ticketData.field.map((field, index) => (
                        <li key={index}>{field}</li>
                      ))}
                    </ul>
                  ) : 'Not specified'
                )
              ) : 'Not specified'}
            </div>
          </div>
          <div className="info-item">
            <div className="info-label">Degree:</div>
            <div className="info-value" id="ticketDegreeValue">{ticketData.degree || 'Not specified'}</div>
          </div>
        </div>
      ) : (
        <div id="ticketEdit">
          <div className="form-group">
            <label className="form-label">Location:</label>
            <select 
              className="form-input" 
              id="ticketLocationInput" 
              name="location"
              value={formData.location}
              onChange={handleInputChange}
            >
              <option value="">Select District</option>
              {districts.map(district => (
                <option key={district} value={district}>{district}</option>
              ))}
            </select>
          </div>
          <div className="form-group">
            <label className="form-label">Experience (Years):</label>
            <input 
              type="number" 
              className="form-input" 
              id="ticketExperienceInput" 
              name="experience"
              value={formData.experience}
              onChange={handleInputChange}
              min="0"
              max="100"
              required
            />
          </div>
          <div className="form-group">
            <label className="form-label">Remote Work:</label>
            <select 
              className="form-input" 
              id="ticketRemoteInput" 
              name="remoteWork"
              value={formData.remoteWork}
              onChange={handleInputChange}
            >
              <option value="">Select Remote Option</option>
              {remoteOptions.map(option => (
                <option key={option} value={option}>{option}</option>
              ))}
            </select>
          </div>
          <div className="form-group">
            <label className="form-label">Job Type:</label>
            <select 
              className="form-input" 
              id="ticketJobTypeInput" 
              name="jobType"
              value={formData.jobType}
              onChange={handleInputChange}
            >
              {jobTypes.map(type => (
                <option key={type} value={type}>{type}</option>
              ))}
            </select>
          </div>
          <div className="form-group">
            <label className="form-label">Field:</label>
            <div className="field-input">
              <select
                className="form-input"
                value={selectedField}
                onChange={(e) => setSelectedField(e.target.value)}
              >
                <option value="">Select a field</option>
                {fields.map((field, index) => (
                  <option key={index} value={field}>
                    {field}
                  </option>
                ))}
              </select>
              <button
                type="button"
                className="btn btn-secondary btn-sm"
                onClick={handleAddField}
                disabled={!selectedField}
              >
                <i className="fas fa-plus"></i> Add
              </button>
            </div>
            
            {formData.field && formData.field.length > 0 && (
              <div className="field-list">
                <h4>Selected Fields:</h4>
                <ul className="field-tags">
                  {formData.field.map((field, index) => (
                    <li key={index} className="field-tag">
                      <span>{field}</span>
                      <button
                        type="button"
                        className="remove-field"
                        onClick={() => handleRemoveField(index)}
                      >
                        <i className="fas fa-times"></i>
                      </button>
                    </li>
                  ))}
                </ul>
              </div>
            )}
          </div>
          <div className="form-group">
            <label className="form-label">Degree:</label>
            <select 
              className="form-input" 
              id="ticketDegreeInput" 
              name="degree"
              value={formData.degree}
              onChange={handleInputChange}
            >
              <option value="">Select Degree</option>
              {degreeOptions.map(degree => (
                <option key={degree} value={degree}>{degree}</option>
              ))}
            </select>
          </div>
          <div className="form-actions">
            <button className="btn btn-primary" onClick={handleSave}>Save</button>
            <button className="btn btn-secondary" onClick={handleCancel}>Cancel</button>
          </div>
        </div>
      )}
    </div>
  );
};

export default CandidateTicketSection;
