import React from 'react';

const ApplicationsSection = ({ applications, onRefreshApplications, onViewApplication, onWithdrawApplication, onPrepareForInterview, onApplyAgain, onActionStatus, onViewCandidateProfile, title, recruiterMode }) => {
  const getStatusClass = (status) => {
    switch (status) {
      case 'Applied': return 'status-applied';
      case 'Interview Scheduled': return 'status-interview';
      case 'Not Selected': return 'status-rejected';
      default: return 'status-applied';
    }
  };

  const getProgressWidth = (status) => {
    switch (status) {
      case 'Applied': return '25%';
      case 'Interview Scheduled': return '60%';
      case 'Not Selected': return '100%';
      default: return '25%';
    }
  };

  const getProgressText = (status) => {
    switch (status) {
      case 'Applied': return 'Application Review';
      case 'Interview Scheduled': return 'Interview Process';
      case 'Not Selected': return 'Process Complete';
      default: return 'Application Review';
    }
  };

  const getStatusIcon = (status) => {
    switch (status) {
      case 'Applied': return 'fas fa-paper-plane';
      case 'Interview Scheduled': return 'fas fa-comments';
      case 'Not Selected': return 'fas fa-times-circle';
      default: return 'fas fa-paper-plane';
    }
  };

  const handleAction = (action, appId) => {
    switch (action) {
      case 'view':
        onViewApplication(appId);
        break;
      case 'accept':
        onActionStatus && onActionStatus(appId, 'ACCEPTED');
        break;
      case 'deny':
        onActionStatus && onActionStatus(appId, 'DENIED');
        break;
      case 'assess':
        onActionStatus && onActionStatus(appId, 'ASSESS');
        break;
      case 'withdraw':
        onWithdrawApplication(appId);
        break;
      case 'prepare':
        onPrepareForInterview(appId);
        break;
      case 'applyAgain':
        onApplyAgain(appId);
        break;
      default:
        break;
    }
  };

  return (
    <div className="section-card applications-section">
      <div className="section-header">
        <h3 className="section-title">{title || 'My Job Applications'}</h3>
      </div>
      <div className="applications-list" id="applicationsList">
        {applications.length === 0 ? (
          <div className="empty-state">
            <i className="fas fa-briefcase"></i>
            <p>No job applications yet. Start applying to jobs to see them here!</p>
          </div>
        ) : (
          applications.map((application, index) => (
            <div key={index} className="application-item">
              <div className="application-header">
                {recruiterMode ? (
                  <h4 
                    className="application-title candidate-name-clickable"
                    onClick={() => {
                      console.log('Candidate name clicked, candidateId:', application.candidateId);
                      console.log('onViewCandidateProfile function:', onViewCandidateProfile);
                      onViewCandidateProfile && onViewCandidateProfile(application.candidateId);
                    }}
                    title="Click to view candidate profile"
                  >
                    {application.title}
                  </h4>
                ) : (
                  <h4 className="application-title">{application.title}</h4>
                )}
                <span className="application-company">{application.company}</span>
              </div>
              <div className="application-details">
                <div className={`application-status ${getStatusClass(application.status)}`}>
                  <i className={getStatusIcon(application.status)}></i>
                  {application.status}
                </div>
                <div className="application-date">
                  <i className="fas fa-calendar"></i>
                  Applied on {application.date}
                </div>
              </div>
              <div className="application-actions">
                {!recruiterMode && (
                  <button 
                    className="btn btn-success compact" 
                    onClick={() => handleAction('view', application.id)}
                  >
                    <i className="fas fa-eye"></i> View Details
                  </button>
                )}
                {!recruiterMode && application.status === 'Applied' && (
                  <button 
                    className="btn btn-primary" 
                    onClick={() => handleAction('withdraw', application.id)}
                  >
                    <i className="fas fa-times"></i> Withdraw
                  </button>
                )}
                {recruiterMode && (
                  <>
                    {(() => {
                      const status = (application.status || '').toUpperCase();
                      const activeAction = status === 'ACCEPTED' ? 'accept' : status === 'DENIED' ? 'deny' : status === 'ASSESS' ? 'assess' : null;
                      const acceptClass = `btn ${activeAction === 'accept' ? 'btn-primary' : 'btn-ghost'}`;
                      const denyClass = `btn ${activeAction === 'deny' ? 'btn-primary' : 'btn-ghost'}`;
                      const assessClass = `btn ${activeAction === 'assess' ? 'btn-primary' : 'btn-ghost'}`;
                      return (
                        <>
                          <button 
                            className={acceptClass}
                            onClick={() => handleAction('accept', application.id)}
                          >
                            <i className="fas fa-check"></i> Accept
                          </button>
                          <button 
                            className={denyClass}
                            onClick={() => handleAction('deny', application.id)}
                          >
                            <i className="fas fa-times"></i> Deny
                          </button>
                          <button 
                            className={assessClass}
                            onClick={() => handleAction('assess', application.id)}
                          >
                            <i className="fas fa-user-check"></i> Assess
                          </button>
                        </>
                      );
                    })()}
                  </>
                )}
                {application.status === 'Interview Scheduled' && (
                  <button 
                    className="btn btn-primary" 
                    onClick={() => handleAction('prepare', application.id)}
                  >
                    <i className="fas fa-video"></i> Prepare
                  </button>
                )}
                {application.status === 'Not Selected' && (
                  <button 
                    className="btn btn-primary" 
                    onClick={() => handleAction('applyAgain', application.id)}
                  >
                    <i className="fas fa-redo"></i> Apply Again
                  </button>
                )}
              </div>
            </div>
          ))
        )}
      </div>
    </div>
  );
};

export default ApplicationsSection;
