import React from 'react';

const RecruiterJobOffersSection = ({ jobOffers, onRefreshOffers, onViewOffer, onEditOffer, onDeleteOffer, title, onFindCandidates }) => {
  const getStatusClass = (status) => {
    switch (status) {
      case 'Pending':
        return 'pending';
      case 'Accepted':
        return 'accepted';
      case 'Declined':
        return 'declined';
      case 'Expired':
        return 'expired';
      default:
        return 'pending';
    }
  };

  const getStatusIcon = (status) => {
    switch (status) {
      case 'Pending':
        return 'fas fa-clock';
      case 'Accepted':
        return 'fas fa-check-circle';
      case 'Declined':
        return 'fas fa-times-circle';
      case 'Expired':
        return 'fas fa-exclamation-triangle';
      default:
        return 'fas fa-clock';
    }
  };

  const getProgressWidth = (status) => {
    switch (status) {
      case 'Pending':
        return '25%';
      case 'Accepted':
        return '100%';
      case 'Declined':
        return '0%';
      case 'Expired':
        return '0%';
      default:
        return '25%';
    }
  };

  const getProgressText = (status) => {
    switch (status) {
      case 'Pending':
        return 'Offer sent - awaiting candidate response';
      case 'Accepted':
        return 'Offer accepted - congratulations!';
      case 'Declined':
        return 'Offer declined by candidate';
      case 'Expired':
        return 'Offer expired';
      default:
        return 'Offer sent - awaiting candidate response';
    }
  };

  const handleAction = (action, offerId) => {
    switch (action) {
      case 'view':
        onViewOffer(offerId);
        break;
      case 'edit':
        onEditOffer(offerId);
        break;
      case 'delete':
        onDeleteOffer(offerId);
        break;
      default:
        break;
    }
  };

  return (
    <div className="section-card recruiter-job-offers-section job-offers-section">
      <div className="section-header">
        <h3 className="section-title">
          <i className="fas fa-handshake"></i>
          {title || 'JOB OFFERS'}
        </h3>
        {onFindCandidates && (
          <button className="find-candidates-btn" onClick={onFindCandidates}>
            <i className="fas fa-search"></i> Find Candidates
          </button>
        )}
      </div>
      <div className="offers-list" id="recruiterOffersList">
        {jobOffers.length === 0 ? (
          <div className="empty-state">
            <i className="fas fa-gift"></i>
            <p>No job offers sent yet. Start offering positions to candidates!</p>
          </div>
        ) : (
          jobOffers.map((offer, index) => (
            <div key={index} className="offer-item">
              <div className="offer-header">
                <div className="offer-title-section">
                  <h4 className="offer-title">{offer.title}</h4>
                  <span className="offer-company">{offer.company}</span>
                  <div className="candidate-info">
                    <i className="fas fa-user"></i>
                    Offered to {offer.candidateName}
                  </div>
                </div>
                <div className={`offer-status ${getStatusClass(offer.status)}`}>
                  <i className={getStatusIcon(offer.status)}></i>
                  {offer.status}
                </div>
              </div>
              
              <div className="offer-details">
                <div className="offer-detail">
                  <i className="fas fa-map-marker-alt"></i>
                  <span>{offer.location}</span>
                </div>
                <div className="offer-detail">
                  <i className="fas fa-dollar-sign"></i>
                  <span>{offer.salary}</span>
                </div>
                <div className="offer-detail">
                  <i className="fas fa-briefcase"></i>
                  <span>{offer.jobType}</span>
                </div>
                <div className="offer-detail">
                  <i className="fas fa-calendar"></i>
                  <span>Offered on {offer.offerDate}</span>
                </div>
              </div>

              {offer.recruiterMessage && (
                <div className="recruiter-message">
                  <div className="message-header">
                    <i className="fas fa-comment"></i>
                    Your message to {offer.candidateName}
                  </div>
                  <p className="message-text">"{offer.recruiterMessage}"</p>
                </div>
              )}

              <div className="job-description">
                <h4>Job Description</h4>
                <p>{offer.description}</p>
              </div>

              <div className="job-requirements">
                <h4>Requirements</h4>
                <p>{offer.requirements}</p>
              </div>

              <div className="offer-progress">
                <div className="progress-bar">
                  <div className="progress-fill" style={{ width: getProgressWidth(offer.status) }}></div>
                </div>
                <span className="progress-text">{getProgressText(offer.status)}</span>
              </div>

              <div className="offer-actions">
                <button 
                  className="btn btn-secondary" 
                  onClick={() => handleAction('view', offer.id)}
                >
                  <i className="fas fa-eye"></i> View Details
                </button>
                {offer.status === 'Pending' && (
                  <button 
                    className="btn btn-primary" 
                    onClick={() => handleAction('edit', offer.id)}
                  >
                    <i className="fas fa-edit"></i> Edit Offer
                  </button>
                )}
                <button 
                  className="btn btn-danger" 
                  onClick={() => handleAction('delete', offer.id)}
                >
                  <i className="fas fa-trash"></i> Delete
                </button>
              </div>
            </div>
          ))
        )}
      </div>
    </div>
  );
};

export default RecruiterJobOffersSection;
